function Main() {
  let [data1, setdata1] = useState(adidas_item);
  let [data2, setdata2] = useState(teaser_arr);
  let [data3, setdata3] = useState(Best_item);
  let [data4, setdata4] = useState(New_item);
  let [count, setCount] = useState(1);
  let [res, setRes] = useState([0, 1, 2, 3, 4, 5, 6, 7, 8]);

  return (
    <>
      <div className="video_wrap">
        <video className="MH_video" muted="muted" autoPlay="autoPlay" playsInline="playsInline" loop="loop">
          <source src={video} type="video/mp4" />
        </video>
      </div>
      <div className="slide_wrap">
        <div className="MH_title">
          <h3>SS23 NEW TREND</h3>
          <a href="/">
            더보기
            <span className="underline"></span>
          </a>
        </div>
        <Swiper slidesPerView={6} slidesPerGroup={6} spaceBetween={20} navigation pagination={{clickable: true}} modules={[Pagination, Navigation]} className="mySwiper">
          {data1.map((ele, i) => (
            <SwiperSlide key={i}>
              <Data data1={data1[i]} i={i}/>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
      <div className="teaser_wrap">
        <Swiper slidesPerView={4} slidesPerGroup={6} spaceBetween={20} navigation modules={[Navigation]}>
          {data2.map((ele, i) => (
            <SwiperSlide key={i}>
              <Teaser data2={data2[i]} />
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
      {/* 정렬 버튼 */}
      <div className="button_wrap">
        <ButtonGroup>
          <Button
            variant="outline-success"
            onClick={() => {
              let copy = [...Best_item].filter((data3) => data3.gender !== "o");
              setdata3(copy);
              console.log(data3);
              let res1 = [];
              for (let i in copy) {
                res1.push(copy[i].id);
              }
              setRes(res1);
            }}
          >
            All
          </Button>
          <Button
            variant="outline-success"
            onClick={() => {
              let copy = [...Best_item].filter((data3) => data3.gender === "Men");
              setdata3(copy);
              console.log(data3);
              let res1 = [];
              for (let i in copy) {
                res1.push(copy[i].id);
              }
              setRes(res1);
            }}
          >
            Men's
          </Button>
          <Button
            variant="outline-success"
            onClick={() => {
              let copy = [...Best_item].filter((data3) => data3.gender === "Women");
              setdata3(copy);
              let res1 = [];
              for (let i in copy) {
                res1.push(copy[i].id);
              }
              setRes(res1);
            }}
          >
            Women's
          </Button>
          <Button
            variant="outline-success"
            onClick={() => {
              let copy = [...Best_item].filter((data3) => data3.gender === "Kid");
              setdata3(copy);
              console.log(data3);
              let res1 = [];
              for (let i in copy) {
                res1.push(copy[i].id);
              }
              setRes(res1);
            }}
          >
            Kid's
          </Button>
        </ButtonGroup>
      </div>
      {/* 정렬되야하는 상품 이미지 */}
      <div className="view_wrap">
        <Title />
        <div className="best_item_wrap">
          <Swiper slidesPerView={5} slidesPerGroup={2} spaceBetween={20} navigation modules={[Navigation]}>
            {data3.map((ele, i) => (
              <SwiperSlide key={i}>
                <Main_Data data3={data3[i]} />
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
      <div className="video_wrap">
        <video className="MH_sub_video" muted="muted" autoPlay="autoPlay" playsInline="playsInline" loop="loop">
          <source src={video2} type="video/mp4" />
        </video>
        <div className="sub_title_wrap">
          <h1>COOL DOWN SEOUL</h1>
          <p>마라톤 완주의 기쁨을 안고, 지금 서울은 쿨다운 할 시간.</p>
          <span className="buy_btn_num1">구매하기</span>
          <span className="buy_btn_arrow"></span>
        </div>
      </div>
      <Container className="New_item_title">
        <h3>New Item</h3>
        <Row>
          {data4.map((ele, i) => (
            <New_item_slidess key={i} data4={data4[i]} />
          ))}
        </Row>
      </Container>
      <div className="item_plus_btn">
        <button
          className="Temporary"
          onClick={() => {
            if (count === 1) {
              axios.get("https://raw.githubusercontent.com/Nubeez/adidas_item_axios/main/plus_item.json").then((result) => {
                console.log(result);
                let copy = [...data4, ...result.data];
                console.log(copy);
                setdata4(copy);
                setCount(2);
              });
            } else if (count === 2) {
              axios.get("https://nubeez.github.io/adidas_item_axios/plus_item2.json").then((result) => {
                console.log(result);
                let copy = [...data4, ...result.data];
                setdata4(copy);
                setCount(3);
              });
            } else {
              alert("더이상 상품이 없습니다.");
            }
          }}
        >
          더보기+
        </button>
      </div>
      <Footer2 />
      <Footer />
    </>
  );
}
