import {Container, Row, Col, Button} from "react-bootstrap";
import {useParams} from "react-router-dom";
import {ReactComponent as Star} from "./star.svg";

function Detail(props) {
  let {id} = useParams();
  return (
    <Container className="per100">
      <Row>
        <Col md={9}>
          <img src={process.env.PUBLIC_URL + props.data1[id].imgUrl} width="50%" />
          <img src={process.env.PUBLIC_URL + props.data1[id].imgUrl} width="50%" />
          <img src={process.env.PUBLIC_URL + props.data1[id].imgUrl} width="50%" />
          <img src={process.env.PUBLIC_URL + props.data1[id].imgUrl} width="50%" />
        </Col>
        <Col md={3} className="sidebar_wrap">
          <div className="pt-5 category_size">
            {props.data1[id].category}
            <div>
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
              {<Star style={{verticalAlign: "baseline"}} width="17px" fill="black"></Star>}
            </div>
          </div>
          <h4 className="pt-3">{props.data1[id].title}</h4>
          <p className="font_price_style">{props.data1[id].price.toLocaleString("kr") + " 원"}</p>
          <p>{props.data1[id].content}</p>
          <p className="font_price_style">수량</p>
          <p>{<div></div>}</p>
          <span className="buy_btn_num2">장바구니 담기</span>
        </Col>
      </Row>
    </Container>
  );
}

export default Detail;
